
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'


export default {
components: {
XHeader,
Group,
Cell,
},
data () {
return {
menus: {
menu1: 'Take Photo',
menu2: 'Choose from photos'
},
typeList:[
{
name:"未使用",
click:true,
},
{
name:"已使用",
click:false
},
{
name:"已过期",
click:false
}
]
}
},

methods:{
routerBack(){
this.$router.goBack();
},
typeClick(item){
for(let item of this.typeList) {
item.click=false;
}
item.click=true;
}
}
}



